import React, { Component, Fragment} from 'react';

export default class Index extends Component{
    constructor(props){
        super(props);

        this.state = {
            projectName: 'xui-react'
        }
    }

    render(){
        return(
            <Fragment>
                <h1 id=""></h1>
                <h2 id="-xui">: <code>{this.state.projectName}</code></h2>
                <ul>
                    <li>
                        <p>: <code>reactJs16.3.0</code>,,,.</p>
                    </li>
                    <li>
                        <p>: <code>reactJs</code></p>
                    </li>
                    <li>
                        <p>: IE9</p>
                    </li>
                    <li>
                        <p>: (<code>react</code>)<code>plugins</code></p>
                    </li>
                </ul>
                <h5>,,.</h5>
                <h5></h5>
                <h6>: <em>me@xumengzi.top</em></h6>
            </Fragment>
        )
    }
}


